class Api {
    constructor(config = {}) {
        this.init(config);
    }

    init(config = {}) {
        let baseUrl;

        baseUrl = localStorage.getItem('dataviz.api.url') || config.url;
        this.token = localStorage.getItem('dataviz.api.token') || config.token;

        if (baseUrl.charAt(baseUrl.length - 1) === '/') {
            baseUrl = baseUrl.slice(0, baseUrl.lastIndexOf('/'));
        }

        this.baseUrl = baseUrl;
    }

    list(entity, query = false) {
        const token = this.token;

        let uri = new URL(`${this.baseUrl}/api/v1/crime/${entity}`);

        if (query) {
            Object.keys(query).map(key => {
                uri.searchParams.append(key, query[key]);
            });
        }

        return fetch(uri.toString(), {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            }
        }).then(res => res.json());
    }
}

export default Api;

export let api = new Api({
    url: 'http://localhost:1981',
    token: '0a6fd546-9699-4fc3-8ba6-f878b11f0396'
});